import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatCardModule,
         MatButtonModule,
         MatIconModule,
         MatGridListModule,
         MatListModule,
         MatToolbarModule,
         MatDialogModule } from '@angular/material';

import { AppComponent } from './app.component';
import { ProjectCard } from '../project/project-card/project-card.component';
import { ProjectDetail } from '../project/project-detail/project-detail.component';
import { NgxPageScrollModule } from 'ngx-page-scroll';
import { SwiperModule } from '../../node_modules/angular2-useful-swiper';

@NgModule({
  declarations: [
    AppComponent,
    ProjectCard,
    ProjectDetail
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    MatButtonModule,
    MatCardModule,
    MatIconModule,
    MatGridListModule,
    MatListModule,
    MatDialogModule,
    NgxPageScrollModule,
    SwiperModule
  ],
  providers: [],
  bootstrap: [AppComponent],
  entryComponents: [ProjectDetail]
})
export class AppModule {}



